
exports.toLogin =  (data) =>{  
    // console.log(data.data.code)
    let datas = data.data
    // 401
    if(datas&&datas.code ==401){
         uni.showModal({
            title: '',
            content: '',
            success: (res)=> {
                if (res.confirm) {
                    uni.navigateTo({
                        url: "/pages/center/center"
                      });
                } else if (res.cancel) {
                    console.log('');
                }
            }
        });
    }else{
        uni.showToast({
            title: data.message,
            icon:'none',
            duration: 1000,
      
        });
    }
   


  }